import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  Input,
  Renderer2,
  ViewChild,
} from '@angular/core';
import { InputBaseComponent } from '@backbase/ui-ang/base-classes';
import {
  AbstractControl,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator,
  Validators,
} from '@angular/forms';
import { DomAttributesService } from '@backbase/ui-ang/services';
import { DeprecationsService } from '@backbase/foundation-ang/future';

/**
 * @name InputEmailComponent
 *
 * @a11y Current component provide option to pass needed accessibility
 * attributes. You need to take care of properties that are required in your case :
 *  - role
 *  - aria-activedescendant
 *  - aria-describedby
 *  - aria-expanded
 *  - aria-invalid
 *  - aria-label
 *  - aria-labelledby
 *  - aria-owns
 *
 * @description
 * Component that displays a email input.
 */
@Component({
  selector: 'bb-input-email-ui',
  templateUrl: './input-email.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InputEmailComponent),
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => InputEmailComponent),
      multi: true,
    },
  ],
})
export class InputEmailComponent extends InputBaseComponent implements Validator, AfterViewInit {
  /**
   * The placeholder for the email input. Defaults to an empty string;
   */
  @Input() placeholder = '';
  /**
   * The maxLength for the email input.
   */
  @Input() maxLength: number | undefined;
  /**
   * The minLength for the email input.
   */
  @Input() minLength: number | undefined;
  /**
   * Whether the email input is readonly.
   */
  @Input() readonly = false;

  /**
   * The autocomplete value of enclosed input control.
   */
  @Input() autocomplete: 'email' | 'off' | 'on' | undefined;

  @ViewChild('inputEmail') inputEl: ElementRef | undefined;

  constructor(
    private readonly domAttrService: DomAttributesService,
    private readonly elem: ElementRef,
    private readonly renderer2: Renderer2,
    protected readonly cd: ChangeDetectorRef,
    protected readonly deprecationsService: DeprecationsService,
  ) {
    super(cd, deprecationsService);
  }

  validate(control: AbstractControl): ValidationErrors | null {
    return Validators.email(control);
  }

  ngAfterViewInit(): void {
    if (!this.ariaLabel) {
      this.domAttrService.moveAriaAttributes(
        this.elem.nativeElement,
        this.inputEl && this.inputEl.nativeElement,
        this.renderer2,
      );
    }
  }
}
